import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Card,
  CardContent,
  Button,
  Chip,
  Grid,
  Alert,
  CircularProgress,
} from '@mui/material';
import { useParams, useNavigate, useLocation } from 'react-router-dom';
import { ArrowBack as ArrowBackIcon, Delete as DeleteIcon } from '@mui/icons-material';
import { TenantRegistrationData } from '../../models/tenant';
import tenantService from '../../services/tenantService';
import DeleteTenantDialog from '../../components/DeleteTenantDialog';
import PageHeader from '../../components/common/PageHeader';

const TenantDetail: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const location = useLocation();
  const [tenant, setTenant] = useState<TenantRegistrationData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [deleting, setDeleting] = useState(false);

  useEffect(() => {
    if (id) {
      fetchTenant(id);
    }
  }, [id]);

  const fetchTenant = async (tenantId: string) => {
    try {
      setLoading(true);
      setError(null);
      
      // Use tenantRegistrationId from navigation state if available
      const tenantRegistrationId = location.state?.tenantRegistrationId || tenantId;
      console.log('Fetching tenant with ID:', tenantRegistrationId);
      
      const tenantData = await tenantService.getTenant(tenantRegistrationId);
      setTenant(tenantData);
    } catch (err) {
      setError('Failed to fetch tenant details');
      console.error('Error fetching tenant:', err);
    } finally {
      setLoading(false);
    }
  };

  const getTierColor = (tier: string) => {
    switch (tier) {
      case 'basic':
        return 'default';
      case 'advanced':
        return 'primary';
      case 'premium':
        return 'secondary';
      default:
        return 'default';
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'active':
        return 'success';
      case 'inactive':
        return 'error';
      case 'pending':
        return 'warning';
      default:
        return 'default';
    }
  };

  const handleDelete = async () => {
    if (!tenant || !id) return;
    
    try {
      setDeleting(true);
      setError(null);
      
      // Construct tenant object like Angular version
      const tenantToDelete = {
        tenantId: id,
        tenantData: {
          tenantName: location.state?.tenantName || '',
          email: location.state?.email || '',
          tier: location.state?.tier || 'basic'
        },
        tenantRegistrationData: {
          tenantRegistrationId: tenant.tenantRegistrationId,
          registrationStatus: tenant.registrationStatus
        }
      };
      
      console.log('Deleting tenant:', tenantToDelete);
      console.log('API URL:', `/tenant-registrations/${tenant.tenantRegistrationId}`);
      
      const result = await tenantService.deleteTenant(tenantToDelete);
      console.log('Delete result:', result);
      
      navigate('/tenants');
    } catch (err: any) {
      console.error('Delete error details:', {
        message: err.message,
        response: err.response?.data,
        status: err.response?.status,
        url: err.config?.url
      });
      setError(`Failed to delete tenant: ${err.response?.data?.message || err.message}`);
    } finally {
      setDeleting(false);
      setDeleteDialogOpen(false);
    }
  };

  if (loading) {
    return (
      <Box className="loading-container">
        <CircularProgress />
      </Box>
    );
  }

  if (error) {
    return (
      <Alert severity="error">
        {error}
      </Alert>
    );
  }

  if (!tenant) {
    return (
      <Alert severity="error">
        Tenant not found
      </Alert>
    );
  }

  return (
    <>
      <div className="page-container">
        <div className="container">
          <PageHeader
            title="Tenant Details"
            subtitle="View and manage tenant registration information"
            actions={
              <Button
                variant="contained"
                startIcon={<DeleteIcon />}
                onClick={() => setDeleteDialogOpen(true)}
                disabled={deleting}
                sx={{
                  borderRadius: 2,
                  px: 3,
                  py: 1.5,
                  fontWeight: 500,
                  textTransform: 'none',
                  backgroundColor: '#4b5563',
                  color: 'white',
                  '&:hover': {
                    backgroundColor: '#374151',
                  }
                }}
              >
                Delete Tenant
              </Button>
            }
          />

          <Grid container spacing={3}>
            <Grid item xs={12}>
              <Card>
                <CardContent>
                  <Typography variant="h6" gutterBottom>
                    Tenant Registration Information
                  </Typography>
                  
                  <Box className="tenant-detail-field">
                    <Typography variant="body2" color="text.secondary">
                      Registration ID
                    </Typography>
                    <Typography variant="body1">
                      {tenant.tenantRegistrationId || 'N/A'}
                    </Typography>
                  </Box>

                  <Box className="tenant-detail-field">
                    <Typography variant="body2" color="text.secondary">
                      Registration Status
                    </Typography>
                    <Chip
                      label={tenant.registrationStatus?.toUpperCase() || 'UNKNOWN'}
                      color={getStatusColor(tenant.registrationStatus?.toLowerCase() || '') as any}
                      size="small"
                    />
                  </Box>

                  <Box className="tenant-detail-field">
                    <Typography variant="body2" color="text.secondary">
                      Tenant ID (from URL)
                    </Typography>
                    <Typography variant="body1">
                      {id}
                    </Typography>
                  </Box>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </div>
      </div>
      
      <DeleteTenantDialog
        open={deleteDialogOpen}
        onClose={() => setDeleteDialogOpen(false)}
        onConfirm={handleDelete}
        isDeleting={deleting}
      />
    </>
  );
};

export default TenantDetail;